<template>
  <div class="user-list">
    <div class="title-users">On-line {{ getOnlineUsersCount }}</div>
    <!--     
    -> v-for Userlist onlineUsers  . -->
    <UserRow
      v-for="user in onlineUserList"
      :key="user.id"
      :nickName="user.username"
      :isBot="user.isBot"
      :profileimage="user.profileimage"
      :isOnline="true"
      @click="toDM(user.id)"
    />

    <div class="title-users">Off-line {{ getOfflineUsersCount }}</div>
    <!--   -->
    <UserRow
      v-for="user in offlineUserList"
      :key="user.id"
      :nickName="user.username"
      :isBot="user.isBot"
      :profileimage="user.profileimage"
      :isOnline="false"
      @click="toDM(user.id)"
    />
  </div>
</template>

<script>
import UserRow from './user-row'
import $axios from 'axios'

export default {
  //  compo Userlist Object()(:)   .
  props: {
    onlineUserList: Object,
    offlineUserList: Object,
    userInfo: Object
  },
  //    
  components: {
    UserRow
  },
  //  data template {{}}     
  //        .
  //      computed      .
  //     methods    computed  .
  // computed          
  //   .      .
  //   method  method      
  //          
  // using 'computed' is better!!! computed -> getter 
  computed: {
    getOnlineUsersCount () {
      return this.onlineUserList.length
    },
    getOfflineUsersCount () {
      return this.offlineUserList.length
    }
  },
  methods: {
    toDM (otherid) {
      if (otherid === this.userInfo.id) {
        alert(' DMd   .')
      }
      console.log('toDM')
      console.log('otherid : ', otherid)
      console.log('userid : ', this.userInfo.id)
      const data = {
        userid: this.userInfo.id,
        otherid: otherid
      }
      $axios
        .post('/api/chat/DM', data)
        .then((response) => {
          this.$emit('changeRoom', 0)
          this.$store.dispatch('module1/setDMInfo', response.data)
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.user-list {
  grid-area: ul;

  display: flex;
  flex-direction: column;

  padding: 3px 6px 10px 16px;
  background-color: var(--secondary);
  max-height: calc(100vh - 46px);
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--tertiary);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-track {
    background-color: var(--primary);
  }
}
.title-users {
  margin-top: 20px;
  color: var(--grey);
  font-weight: 500;
  text-transform: uppercase;
}

</style>
